  left: 2px;
  bottom: 2px;
  width: 36px;
  background-color: #dddddd;
  border-radius: 36px;
  transition: margin 0.4s, background 0.4s; }

input.cmn-toggle-round-flat:checked + label {
  background-color: #8ce196; }

input.cmn-toggle-round-flat:checked + label:after {
  margin-left: 40px;
  background-color: #8ce196; }

body.pigify-page {
  background: #26C281;
  color: rgba(0, 0, 0, 0.45);
  font-size: 100%;
  line-height: 1.25;
  font-family: 'Lato', Arial, sans-serif; }

.wrapper {
  padding: 20px; }

.pigify-page a {
  color: rgba(0, 0, 0, 0.25);
  text-decoration: none;
  outline: none; }

.pigify-page a:hover, .pigify-page a:focus {
  color: rgba(0, 0, 0, 0.6); }

.pigify-page section {
  padding: 5em 2em 10em;
  background: #2dcb89;
  text-align: center; }

@media screen and (max-width: 44.75em) {
  .pigify-page section {
    padding: 1em 2em; } }

@font-face {
  font-weight: normal;
  font-style: normal;
  font-family: 'icomoon';
  src: url("../fonts/icomoon.eot?-9heso");
  src: url("../fonts/icomoon.eot?#iefix-9heso") format("embedded-opentype"), url("../fonts/icomoon.woff?-9heso") format("woff"), url("../fonts/icomoon.ttf?-9heso") format("truetype"), url("../fonts/icomoon.svg?-9heso#icomoon") format("svg"); }

.pigform {
  position: relative;
  margin: 0 auto;
  padding: 2em 0;
  max-width: 860px;
  width: 100%;
  text-align: left;
  font-size: 2.5em; }

.pigform .submit {
  display: none; }

/* Question list style */
.pigform ul {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  -webkit-transition: height 0.4s;
  transition: height 0.4s; }

.pigform ul:before {
  content: '';
  background-color: rgba(0, 0, 0, 0.1);
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2.35em; }

ul li {
  z-index: 100;
  position: relative;
  visibility: hidden;
  height: 0;
  -webkit-transition: visibility 0s 0.4s, height 0s 0.4s;
  transition: visibility 0s 0.4s, height 0s 0.4s; }

ul li.current,
.no-js ul li {
  visibility: visible;
  height: auto;
  -webkit-transition: none;
  transition: none; }

/* Labels */
ul li > span {
  display: block;
  overflow: hidden; }

ul li > span label {
  display: block;
  -webkit-transition: -webkit-transform 0.4s;
  transition: transform 0.4s;
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%); }

ul li.current > span label,
.no-js ul li > span label {
  -webkit-transition: none;
  transition: none;
  -webkit-transform: translateY(0);
  transform: translateY(0); }

.show-next ul li.current > span label {
  -webkit-animation: moveUpFromDown 0.4s both;
  animation: moveUpFromDown 0.4s both; }

@-webkit-keyframes moveUpFromDown {
  from {
    -webkit-transform: translateY(100%); }
  to {
    -webkit-transform: translateY(0); } }

@keyframes moveUpFromDown {
  from {
    -webkit-transform: translateY(100%);
    transform: translateY(100%); }
  to {
    -webkit-transform: translateY(0);
    transform: translateY(0); } }

/* Input field */
ul input {
  display: block;
  margin: 0.3em 0 0 0;
  padding: 0.2em 1em 0.5em 0.7em;
  width: calc(100% - 2em);
  border: none;
  background: transparent;
  color: rgba(0, 0, 0, 0.8);
  font-size: 1em;
  line-height: 1;
  opacity: 0;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
  height: 2.1em; }

ul .current input,
.no-js ul input {
  opacity: 1; }

ul input:focus,
.pigform button:focus {
  outline: none; }

/* Next question button */
.go {
  position: absolute;
  right: 0;
  top: 3.7em;
  /* padding-bottom of form plus progress bar height */
  display: block;
  padding: 0;
  width: 2em;
  height: 2em;
  border: none;
  background: none;
  color: rgba(0, 0, 0, 0.4);
  text-align: center;
  opacity: 0;
  z-index: 100;
  cursor: pointer;
  -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
  transition: transform 0.3s, opacity 0.3s;
  -webkit-transform: translateX(-20%);
  transform: translateX(-20%);
  pointer-events: none;
  -webkit-tap-highlight-color: transparent; }

.go:hover {
  color: rgba(0, 0, 0, 0.5); }

.go.show {
  opacity: 1;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  pointer-events: auto; }

/* Error and final message */
.pigform .error_message,
.pigform .translated_message {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.4s;
  transition: opacity 0.4s; }

.pigform .error_message {
  padding: 0.4em 3.5em 0 0;
  width: 100%;
  color: rgba(0, 0, 0, 0.7);
  font-style: italic;
  font-size: 0.4em; }

.error_message.show,
.translated_message.show {
  visibility: visible;
  opacity: 1; }

.translated_message.show {
  -webkit-transition-delay: 0.5s;
  transition-delay: 0.5s; }

/* Final hiding of form / showing message */
.pigform_inner.hide {
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.3s, visibility 0s 0.3s;
  transition: opacity 0.3s, visibility 0s 0.3s; }

/* No JS Fallback */
.no-js .pigform {
  font-size: 1.75em; }

.no-js ul li {
  padding: 0 0 2em; }

.no-js .pigform .submit {
  display: block;
  float: right;
  padding: 10px 20px;
  border: none;
  background: rgba(0, 0, 0, 0.3);
  color: rgba(0, 0, 0, 0.4); }

.no-js .pigform .controls {
  display: none; }

/* Remove IE clear cross */
input[type=text]::-ms-clear {
  display: none; }

/* Adjust form for smaller screens */
@media screen and (max-width: 44.75em) {
  .pigform {
    font-size: 1.8em; } }

@media screen and (max-width: 33.5625em) {
  .pigform {
    font-size: 1.2em; } }

